import { HttpClient } from "@angular/common/http";
import { Component } from "@angular/core";
import { NavController, NavParams, LoadingController, AlertController, ModalController } from "ionic-angular";
import { Storage } from "@ionic/storage";
import { SermonsProvider } from "../../providers/sermons/sermons";
import { FileTransfer, FileTransferObject } from "@ionic-native/file-transfer";
import { File } from "@ionic-native/file";
import { Toast } from "@ionic-native/toast";
import { Network } from "@ionic-native/network";
import moment from "moment";
import { LoadingBarService } from "@ngx-loading-bar/core";


@Component({
  selector: "page-sermon-details",
  templateUrl: "sermon-details.html"
})
export class SermonDetailsPage {
  sermon: any;
  progress: any = 0;
  isDownloaded: Boolean = false;
  user: any;
  voucher: any;
  voucherVerified: boolean = false;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public storage: Storage,
    private network: Network,
    private file: File,
    private transfer: FileTransfer,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    public toast: Toast,
    public sermonProvider: SermonsProvider,
    private http: HttpClient,
    private modalCtrl: ModalController,
    private loadingBar: LoadingBarService
  ) {
    this.sermon = navParams.get("sermon");
    this.storage.get("user").then(user => {
      this.user = user;
      this.init();
    });
    this.storage.get("downloads").then(downloads => {
      if (downloads !== null) {
        downloads.forEach(element => {
          if (element.title.toLowerCase() === this.sermon.title.toLowerCase()) {
            this.isDownloaded = true;
          }
        });
      }
    });
  }

  init() {
    this.storage.get("voucher").then(voucher => {
      if (voucher !== null) {
        this.voucher = voucher;
        if (
          !voucher.isExpired &&
          moment(voucher.expiry).isSameOrAfter(moment())
        ) {
          this.voucherVerified = true;
        }
        if (moment(voucher.expiry).isBefore(moment())) {
          this.voucherExpired();
        }
      } else {
        if (
          this.user !== null &&
          this.user.sub_active &&
          moment(this.user.sub_end).isSameOrAfter(moment())
        ) {
          this.voucherVerified = true;
        }
        if (
          this.user !== null &&
          this.user.sub_active &&
          moment(this.user.sub_end).isBefore(moment())
        ) {
          this.userExpired();
        }
      }
    });
  }

  playMedia() {
    // if (this.voucherVerified === false) {
    //   const modal = this.modalCtrl.create('VoucherCheckPage');
    //   modal.present();
    // } else {
    //   this.navCtrl.push('PlayerPage', { sermon: this.sermon})
    // }
    this.navCtrl.push("PlayerPage", { sermon: this.sermon });
  }

  preDownload() {
    // const modal = this.modalCtrl.create("VoucherCheckPage");
    // modal.present();
    if (this.voucherVerified === false) {
      const modal = this.modalCtrl.create("VoucherCheckPage");
      modal.present();
    } else {
      this.preDownloadCheck();
    }
  }

  preDownloadCheck() {
    if (this.network.type == "wifi") {
      this.downloadSermon();
    } else if (this.network.type === "none") {
      const alert = this.alertCtrl.create({
        subTitle: "You need an internet connection to download sermons.",
        buttons: ["OK"]
      });
      alert.present();
    } else {
      const confirm = this.alertCtrl.create({
        message:
          "You are NOT connected to a WiFi, are you sure you want to download this sermon using your data connection?",
        buttons: [
          {
            text: "Cancel",
            handler: () => {
              console.log("Disagree clicked");
            }
          },
          {
            text: "Yes, please",
            handler: () => {
              this.downloadSermon();
            }
          }
        ]
      });
      confirm.present();
    }
  }

  downloadSermon() {
    this.toast.showLongBottom("Download initiated...").subscribe(() => {});
    this.loadingBar.start();
    let fileTransfer: FileTransferObject = this.transfer.create();
    const url = this.sermon.audio;
    const fn =
      this.file.dataDirectory + url.substring(url.lastIndexOf("/") + 1);
    console.log("download location", fn);

    fileTransfer.download(url, fn).then(
      entry => {
        this.saveDownloaded(entry);
        this.updateVoucherUsage();
        console.log("downloaded 2");
        this.toast
          .showLongBottom("Sermon successfully downloaded.")
          .subscribe(() => {});
      },
      error => {
        console.log("transfer", error);
        this.loadingBar.stop();
        this.toast
          .showLongBottom("Network Error, try initiating the download again.")
          .subscribe(_ => {});
      }
    );

    fileTransfer.onProgress(progressEvent => {
      if (progressEvent.lengthComputable) {
        var percentage = Math.round(
          (progressEvent.loaded / progressEvent.total) * 100
        );
        this.progress = percentage;
        // console.log('Downloaded:  ' + percentage + '%, - ' + typeof percentage);
        if (percentage === 100) {
          this.isDownloaded = true;
          this.loadingBar.complete();
          console.log("downloaded 1");
        }
      }
    });
  }

  saveDownloaded(entry) {
    let sermon = {
      title: this.sermon.title,
      image: this.sermon.image,
      preacher: this.sermon.preacher,
      audio: entry.toURL()
    };
    this.storage.get("downloads").then(downloads => {
      if (downloads === null) {
        let down = [];
        down.push(sermon);
        this.storage.set("downloads", down);
      } else {
        let newDown: [Object] = downloads;
        newDown.push(sermon);
        this.storage.set("downloads", newDown);
      }
    });
  }

  // Set user sub status to expired
  userExpired() {
    this.voucherVerified = false;
    this.http
      .post("https://realm-glory.herokuapp.com/api/user/expired", {
        user: this.user._id
      })
      .subscribe(status => {
        console.log(status);
        if (status["status"] === "success") {
          this.storage.set("user", status["data"]);
        }
      });
  }

  voucherExpired() {
    this.voucherVerified = false;
    this.http
      .post("https://realm-glory.herokuapp.com/api/voucher/expired", {
        voucher: this.voucher._id
      })
      .subscribe(status => {
        console.log(status);
        if (status["status"] === "success") {
          this.storage.set("voucher", status["data"]);
        }
      });
  }

  updateVoucherUsage() {
    this.http
      .post("https://realm-glory.herokuapp.com/api/voucher/usage", {
        user: this.user ? this.user : null,
        voucher: this.voucher ? this.voucher : null
      })
      .subscribe(status => {
        console.log("voucher used");
        if (status["status"] === "success") {
          if (status["user"]) {
            this.storage.set("user", status["user"]);
          } else {
            this.storage.set("voucher", status["voucher"]);
          }
        }
      });
  }

  shareSermon() {
    // this.sermonProvider.shareSermon(this.sermon);
  }
}



